window.dom = {
    // create: function(){},
    create(string){//Áî®‰∫éÂàõÂª∫ËäÇÁÇπ
        // return document.createElement(string)
        // const container = document.createElement('div')
        const container = document.createElement("template")
        container.innerHTML = string.trim(); //trimÂéªÁ©∫Ê†º
        // console.log("test")
        // console.dir(container)
        // return container.children[0]
        return container.content.firstChild
    },
    after(node,node2){ //Â∞Ünode2ÊèíÂÖ•Âà∞nodeÂêéÈù¢
        // insertBefore() ÊñπÊ≥ïÂú®ÊÇ®ÊåáÂÆöÁöÑÂ∑≤ÊúâÂ≠êËäÇÁÇπ‰πãÂâçÊèíÂÖ•Êñ∞ÁöÑÂ≠êËäÇÁÇπ
        // node.parentNode.insertBefore(node,node2) //Â∞ÜnodeÊèíÂÖ•node2ÂâçÈù¢
        node.parentNode.insertBefore(node2,node.nextSibling) //Â∞Ünode2ÊèíÂÖ•Âà∞node‰∏ã‰∏Ä‰∏™ÂÖÉÁ¥†‰πãÂâç
    },
    before(node,node2){//Â∞Ünode2ÊèíÂÖ•Âà∞nodeÂâçÈù¢
        node.parentNode.insertBefore(node2,node)
    },
    append(parent, node){//Áî®‰∫éÊñ∞Â¢ûÂÑøÂ≠êËäÇÁÇπ
        parent.appendChild(node)
    },
    wrap(node, parent){//Áî®‰∫éÊñ∞Â¢ûÁà∏Áà∏ËäÇÁÇπ
        dom.before(node,parent) //Â∞ÜparentËäÇÁÇπÊèíÂÖ•Âà∞nodeËäÇÁÇπ‰πãÂâç
        dom.append(parent,node) //Â∞ÜnodeËäÇÁÇπÊèíÂÖ•Âà∞parentËäÇÁÇπÈáåÈù¢
    },
    remove(node){
        node.parentNode.removeChild(node)
        return node //‰øùÁïôËäÇÁÇπÂºïÁî®
    },
    empty(node){
        const childNodes = node.childNodes
        // const {childNodes} = node //Ëß£ÊûÑËµãÂÄº
        const array = []
        // for(let i = 0; i < childNodes.length; i++){
        //     console.log(childNodes.length) // length‰ºöÂèòÂåñ
        //     dom.remove(childNodes[i])
        //     array.push(childNodes[i])
        // }
        let x = node.firstChild
        while(x){
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array //‰øùÁïôËäÇÁÇπÂºïÁî®
    },
    attr(node, name, value){ //ÂáΩÊï∞ÈáçËΩΩ
        if(arguments.length === 3){
            node.setAttribute(name, value)
        }
        if(arguments.length === 2){
            return node.getAttribute(name)
        }
    },
    text(node, string){ //ÈÄÇÈÖç
        // node.innerText = string //ie
        // node.textContent = string //chrome
        if(arguments.length === 2){
            if('innerText' in node){
                node.innerText = string
            }else{
                node.textContent = string
            }   
        }
        if(arguments.length === 1){
            if('innerText' in node){
               return node.innerText
            }else{
               return node.textContent
            }   
        } 
    },
    html(node, string){
        if(arguments.length === 2){
            node.innerHTML = string
        }
        if(arguments.length === 1){
            return node.innerHTML
        }
    },
    style(node, name, value){
        if(arguments.length === 3){
            // dom.style(div, 'color', 'red')
            node.style[name] = value
        }
        if(arguments.length === 2){
            if(typeof name === 'string'){
                //dom.style(div,'color')
                return node.style[name]
            }
            if(name instanceof Object){
                // dom.style(test, {border: '1px solid red', color: 'blue'})
                for(let key in name){
                    // key: border/color
                    // node.style.border = 
                    // node.style.color = 
                    node.style[key] = name[key]
                }
            }
        }
    },
    class:{
        add(node, className){
            node.classList.add(className)
        },
        remove(node, className){
            node.classList.remove(className)
        },
        has(node, className){
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn){
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn){
        node.removeEventListener(eventName, fn)
    },
    find(selector, scope){
        //Â¶ÇÊûúÊúâscopeÂ∞±Áî®scopeË∞ÉÁî®querySelectorAll
        //Âê¶ÂàôÁî®documentË∞ÉÁî®querySelectorAll
        return (scope || document).querySelectorAll(selector)
    },
    parent(node){
        return node.parentNode
    },
    children(node){
        return node.children
    },
    siblings(node){
        return Array.from(node.parentNode.children).filter(n => n!==node)
    },
    next(node){
        let x = node.nextSibling
        while(x && x.nodeType === 3){
            x = x.nextSibling
        }
        return x
    },
    previous(node){
        let x = node.previousSibling
        while(x && x.nodeType === 3){
            x = x.previousSibling
        }
        return x
    },
    each(nodeList, fn){
        for(let i = 0; i < nodeList.length; i++){
            fn.call(null, nodeList[i])
        }
    },
    index(node){
         const list = dom.children(node.parentNode)
         let i
         for(i = 0; i < list.length; i++){
             if(list[i] === node){
                break
             }
         }
         return i
    }
}


